import React from 'react';
import { fnOutput } from '../store/function';

const CompDay = ({ dayData }) => {

  let {
    month, date,  icon, rain, snow, tempMin, tempMax,
    uvi, humidity, pressure
  } = fnOutput(dayData) //   
  
  return (
    <li>

      <b>{month} {date}</b>

      <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
      <b>
        <i className="fa-solid fa-temperature-arrow-up"></i>
        {tempMax}<sup>&#8451;</sup>
      </b>

      <b>
        <i className="fa-solid fa-temperature-arrow-down"></i>
        {tempMin}<sup>&#8451;</sup>
      </b>

      <b>
        <i className="fa-solid fa-arrow-up-wide-short"></i>
        {pressure}<small>hPa</small>
      </b>

      <b>
        <i className="fa-solid fa-sun"></i>
        {uvi}
      </b>

      <b>
        <i className="fa-solid fa-droplet"></i>
        {humidity}<small>%</small>
      </b>

      <b>
        <i className="fa-solid fa-cloud-rain"></i>
        {rain}mm
      </b>

      <b>
        <i className="fa-solid fa-snowflake"></i>
        {snow}mm
      </b>



    </li>
  );
};

export default CompDay;