import React, { useContext } from 'react';
import { AppContext } from '../App';
import CompHour from './CompHour';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import { fnOutput } from '../store/function';
import ChartDataLabels from 'chartjs-plugin-datalabels';

const CompHourly = () => {
  let { stWeather } = useContext(AppContext)
  if (!stWeather) return false; //   ..    ...
  let hourlyArr = stWeather.hourly
  /*chart------------------------------- */
  let labelsArr = new Array(24).fill('')//24 ]
  let dataArr = hourlyArr.map((v, i) => {
    if (i <= 23) { //temp 23 
      let { temp } = fnOutput(v) // 
      return temp
    }
  })//map
  dataArr.splice(24, 24)//   ..48 24 ...


  const data = {
    labels: labelsArr,
    datasets: [{
      label: 'myGraph',
      data: dataArr,
      borderWidth: 1,
      fill: false,
      borderColor: 'rgb(255, 255, 255)',
      tension: 0.2,
    }]
  }
  const options = {
    scales: {
      x: { // 
        display: false,
        grid: {
          display: false,
        },
      },
      y: {
        display: true,
        beginAtZero: true,
        min: -50,
        max: 50,
        stepSize: 10,
        grid: {
          display: true,
          color: "rgba(255,255,255,0.3)",
        },
        ticks: {//  
          display: false,
          stepSize: 10,
          color: "#FFF",
        },
      },
    },
    plugins: {
      legend: {
        display: false,
        position: 'top',
        labels: {
          color: 'rgb(255, 99, 132)'
        }
      }, //legend

      datalabels: {
        display: true,
        color: "white",
        align: 'top',
      }//datalabels
    },
    responsive: true,
    maintainAspectRatio: false,
  }

  /**------------------------------- */


  let gridArr = []
  for (let i = 5; i >= -5; i--) {
    gridArr.push(i * 10)
  }

  return (
    <section className='hourly-section'>
      <div className="inner scr-style">
        <h2>
          <i className="fa-solid fa-clock"></i>
          <span>Hourly Forecast</span>
        </h2>
        <div className="hour-container">
          <ul>
            {
              hourlyArr.map((v, i) => {
                if (i <= 23) {
                  return <CompHour hourData={v} key={i} />// key 
                }
              })
            }
          </ul>
          <div className="chart">
            <Chart type='line' data={data} options={options} plugins={[ChartDataLabels]} />
            <div className='grid'>
              {
                gridArr.map((v,i)=>{
                  return <span key={i}>{v}</span>
                })
              }
              
            </div>
          </div> {/*chart*/}
        </div>
      </div>{/*inner*/}
    </section>
  );
};

export default CompHourly;