import React from 'react';
import { fnOutput } from '../store/function';

const CompHour = ({ hourData }) => {//  hourData  ..CompHouly.jsx
  let {
    month, date, hour, minute, icon, rain, snow
  } = fnOutput(hourData) //   
  return (
    <li>
      <div>
        <b>{month} {date}</b>
        <b>{hour} : {minute}</b>
        <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
        <b>
          <i className="fa-solid fa-cloud-rain"></i>
          {rain}mm
        </b>
        <b>
          <i className="fa-solid fa-snowflake"></i>
          {snow}mm
        </b>
      </div>
    </li>
  );
};

export default CompHour;